<script>
/**
 * vuex管理登陆状态，具体可以参考官方登陆模板示例
 */
import { mapMutations } from 'vuex';
import { getPlatform } from 'utils'
export default {
	onLaunch: function(options) {
		// uni.clearStorage()
		this.setSecret(options);
		// #ifdef MP
		if (uni.getStorageSync('applyDsshopOpenid')) {
			this.checkSession();
		} else {
			this.getLogin();
		}
		// #endif
		
	},
	methods: {
		...mapMutations(['login']),
		setSecret(options) {
			let userInfo = uni.getStorageSync('dsshopUserInfo') || '';
			if (userInfo && uni.getStorageSync('dsshopApplytoken')) {
				//更新登陆状态
				uni.getStorage({
					key: 'dsshopUserInfo',
					success: res => {
						this.login(res.data);
					}
				});
			}
			var applySecret = '', host = "http://127.0.0.1:88/api/v1/app/" // 服务器后端地址
			if (JSON.stringify(options) != '{}' && options.query  && JSON.stringify(options.query) != '{}') {
				if(options.host){	//防止刷新某个页面后applyDsshopSecret失效问题
					applySecret = options;
					applySecret.host = host;
					uni.setStorageSync('applyDsshopSecret', applySecret);
				}else{
					this.setApplySecret(applySecret,host)
				}
				
			} else {
				this.setApplySecret(applySecret,host)
			}
		},
		// 设置应用配置
		setApplySecret(applySecret,host){
			
			if (!uni.getStorageSync('applyDsshopSecret')) {
				applySecret = {
					secret: 'base64:edGHe7j+Kv0GH6HKPFvFEiWUAFfG68ywq1Yy2L0zz+8=', //默认应用Secret
					host: host,
					name: 'Dsshop', // 平台默认名称
				};
				uni.setStorageSync('applyDsshopSecret', applySecret);
			} else {
				applySecret = uni.getStorageSync('applyDsshopSecret');
			}
		},
		// 检测登录状态是否过期
		checkSession() {
			let that = this;
			uni.checkSession({
				success() {},
				fail() {
					that.getLogin();
				}
			});
		},
		// 登录
		getLogin() {
			let that = this;
			let applySecret = uni.getStorageSync('applyDsshopSecret');
			uni.login({
				success(res) {
					if (res.code) {
						uni.request({
							url: applySecret.host + 'miniLogin',
							data: {
								code: res.code,
								platform: getPlatform()
							},
							method: 'POST',
							header: {
								'Content-Type': 'application/json',
								'apply-secret': uni.getStorageSync('applyDsshopSecret').secret,
								// #ifndef H5
								openid: uni.getStorageSync('applyDsshopOpenid')
								// #endif
							},
							success: res => {
								if (res.statusCode === 200) {
									if (res.data.openid) {
										uni.setStorageSync('applyDsshopSession_key', res.data.session_key);
										uni.setStorageSync('applyDsshopOpenid', res.data.openid);
									}
								} else {
									uni.showToast({
										icon: 'none',
										title: res.data.message,
										duration: 2000
									});
								}
							},
							fail: res => {
								uni.showToast({
									title: '服务器无响应',
									duration: 2000
								});
							}
						});
					} else {
						console.log('无响应');
					}
				}
			});
		}
	},
	onShow: function() {},
	onHide: function() {}
};
</script>
